.ViewMap__contener {
    background-color: white;
    min-height: 100vh;
    padding-top: 40px;
}


.ViewMap__grid-calosc {
    margin: 0px 0 10px 0px;
    text-decoration: none;
    text-align: center;
}
.ViewMap__chmury {
    padding-top: 20px;
    font-weight: 700;
    font-size: 22px;
}

.ViewMap__grid-ul {
    list-style-type: none;
    margin-top: 10px;

    li {
        margin: 10px 0 10px 0;
    }
}

.ViewMap__grid-button {
    background-color: rgba(86, 143, 211, 0.85);
    padding: 5px 10px;
    margin-top: 10px;
    width: 170px;
    outline: none;
    cursor: pointer;
    border-radius: 10px;
    a {
        text-decoration: none;
        list-style: none;
        color: black;
    }


}

.ViewMap__grid-button:hover {
    text-decoration: underline;
}

.ViewMap__grid-img > img {
    margin: 10px;
    width: 90%;
    border-radius: 20px;
}





@media(min-width: 600px) {
    .ViewMap__grid {
        display: grid;
        grid-template-columns: repeat(2, 400px);
        margin: 0 auto;
        align-items: center;
        justify-content: center;
    }
    .ViewMap__grid-calosc {
        text-align: center;
        margin-top: 70px;
    }
    .ViewMap__grid-img{
        margin-top: 30px;
    }
    .ViewMap__chmury {
        font-size: 24px;
    }
}

@media ( min-width: 1000px) {
    .ViewMap__grid-img > img {
        margin: 30px 0px 30px 0px;
        width: 400px;
        height: 400px;
    }
    .ViewMap__grid-calosc {
        margin-top: 90px;
    }
    .ViewMap__chmury {
        font-size: 26px;
    }
    .ViewMap__grid-ul {
        font-size: 18px;
    }
    .ViewMap__grid-button {
        padding: 10px 20px;
        margin-top: 10px;
        width: 240px;
        font-size: 18px;
    }
}