.View5day__container {
    min-height: 100vh;
    background-color: #a6c2e2;
    text-align: center;
}
.View5day__input {
    padding: 10px 15px;
    border-radius: 30px;
    border: 1px solid black;
    margin: 20px 0px 30px 20px;
    width: 150px;
}
.piecDni__grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    background-color: white;

}

.piecDni__h3 {
    text-align: center;
    padding: 20px 0;
    font-size: 20px;

}

.piecDni__wyniki {
    font-size: 18px;
    letter-spacing: 1px;
    text-align: center;
    padding-bottom: 20px;
    margin: 0px auto;
    width: 80%;
    margin: 20px auto;
    box-shadow: 0 1px 1px 0 rgba(0,0,0,.06),0 2px 5px 0 rgba(0,0,0,.2);
    max-width: 350px;
}


.piecDni__wyniki > div {
   margin-top: 10px;
}

.piecDni__wyniki > img {
    width: 110px;
    height: 110px;
    margin-right: 10px;
    margin-bottom: 10px;
}
.View5day__pogodaDla {
    font-size: 20px;
    display: flex;
    text-align: center;
    justify-content: center;
}
.View5Day__znacznik {
    margin: 0px 5px;
}

.View5Day__znacznik:first-letter { 
    display: flex;
    text-transform: uppercase;
 }

.pecDni__wiatr {
    -webkit-filter: invert(100%);
    -moz-filter: invert(100%);
    -o-filter: invert(100%);
    -ms-filter: invert(100%);
}


.chartjs-render-monitor {
    background-color: white;
    padding-top: 40px;
    max-width: 900px;
}

@media(min-width:450px) {
    .piecDni__h3 {
        font-size: 22px;
        padding: 22px 0;
    
    }
    
    .piecDni__wyniki {
        font-size: 18px;
    }
    .View5day__input {
        padding: 10px 15px;
        width: 200px;
    }
}

@media(min-width:700px) {

    .piecDni__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        /* grid-template-rows: repeat(3, 350px); */
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        background-color: white;
    }
    .piecDni__wyniki {
        padding-bottom: 35px;
        max-width: 400px;
        font-size: 20px;
    }
    .View5day__pogodaDla {
        font-size: 22px;
    }
    
}

@media(min-width:1100px) {

    .piecDni__grid {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
    }
    .jeden {
        grid-area: 1 / 1 / 2 / 3;
    }
    .dwa {
        grid-area: 1 / 3 / 2 / 5;
    }
    .trzy {
        grid-area: 1 / 5 / 2 / 7;
    }
    .cztery {
        grid-area: 2 / 1 / 3 / 4;
    }
    .piec {
        grid-area: 2 / 4 / 3 / 7;
    }
    
    .piecDni__h3 {
        font-size: 24px;
        padding: 26px 0;
    
    }
  


    .piecDni__wyniki {
        font-size: 20px;
    }
    .piecDni__wyniki div {
        margin-top: 20px;
    }
    .piecDni__wyniki > img {
        width: 80px;
        height: 80px;
    }
    .View5day__pogodaDla {
        font-size: 24px;
        margin-bottom: 20px;
    }
}
