.ViewJakoscPowietrza__styleGrid {
    display: grid;
    grid-template-columns: 100px repeat(2,1fr);
    grid-template-rows: repeat(2,80px);
    background-color: #1B4778;
    color: white;
 @media(min-width: 1000px) {
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(2,120px);

    .smart {
        display: block;
        margin: 30px auto;
        width: 130px;
        height: 130px;

    }
 }
}

.smart {
    width: 100px;
    margin-top: 40px;
    filter: invert(100%);
}

.viewPogoda__div {
  justify-items: center;
}

.opis {
    font-weight: bold;
}

.viewPogodaMiejscowa > div {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 20px 0px;
    align-items: center;
    align-content: center;
    background-color: white;
}
.view-container-weather {
    border-bottom: 4px solid #a6c2e2;
    border-left: 4px solid #a6c2e2;
    
}
.weather-none {
    display: none;
    
}
.viewPogodaImages{
    width: 40px;
    height: 40px;
    margin: 0 auto;
    padding: 0;
}
.border {
    border-bottom: 4px solid #a6c2e2;
    border-left: 4px solid #a6c2e2;
}

.ViewPogodaImagesDiv {
    display: flex;
    border-bottom: 4px solid #a6c2e2;
    border-left: 4px solid #a6c2e2;
}
.ViewPogodaImagesDiv.end {
  display: flex;
  border-right: 4px solid #a6c2e2;
  border-bottom: 4px solid #a6c2e2;
  border-left: 4px solid #a6c2e2;
  
}   
.viewPogodaMiejscowa {
    display: grid;
    margin: 0px;
    padding: 0px;
    grid-template-columns: repeat(3, 1fr) 60px;
    grid-template-rows: repeat(7,60px);
    margin: 0 auto;
    text-align: center;

    .weather-none {
        display: none;
    }
}
.city {
    padding-left: 10px;
    margin-left: 10px;
    font-weight: 700;
}

@media(min-width: 600px) {
    .viewPogodaMiejscowa {
        display:grid;
        font-size: 20px;
        grid-template-columns: 1fr 120px 1fr 120px;
        grid-template-rows: repeat(7,80px);
        .weather-none {
            display: none;
        }
        img {
            width: 60px;
            height: 60px;
        }
    }
}

@media(min-width:800px){
    .viewPogodaMiejscowa {
        grid-template-columns: 1fr repeat(3, 200px);
        grid-template-rows: repeat(5,100px);
        font-size: 20px;
        
        .weather-none {
            display: none;
        }
        img {
            width: 80px;
            height: 80px;
        }

    }
    .viewPogoda__div {
        text-align: center;
        /* style={{paddingLeft: '10px', fontWeight:'700'}}  */
    }
}
@media(min-width:1100px){
    .viewPogodaMiejscowa {
        grid-template-columns: 1fr 200px 150px 180px 180px 150px;
        grid-template-rows: repeat(7,120px);
        font-size: 25px;
        .weather-none {
            display: flex;
            text-align: center;
            justify-content: center;
            align-items: center;
            border-bottom: 4px solid #a6c2e2;
            border-left: 4px solid #a6c2e2;
        }
        div {
            align-items: center;
        }
    }
    .viewPogoda__div {
        text-align: center;
        /* style={{paddingLeft: '10px', fontWeight:'700'}}  */
    }
}